/** @jsxImportSource @emotion/react */
//import { css } from '@emotion/react';
import './App.css';
import { css } from '@emotion/react';

function Loading() {
  return (
    <>
      <div
        css={css`
          background: #ffffff;
          min-height: 85vh;
        `}
        className="d-flex flex-column"
      >
        <div className="d-flex flex-grow-1 justify-content-center align-items-center">
          <div className="text-center">
            <svg
              className="pl"
              viewBox="0 0 200 200"
              width="200"
              height="200"
              xmlns="http://www.w3.org/2000/svg"
            >
              <defs>
                <linearGradient id="pl-grad1" x1="1" y1="0.5" x2="0" y2="0.5">
                  <stop offset="0%" stopColor="hsl(313,0%,80%)" />
                  <stop offset="100%" stopColor="hsl(223,0%,40%)" />
                </linearGradient>
                <linearGradient id="pl-grad2" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="0%" stopColor="hsl(313,0%,80%)" />
                  <stop offset="100%" stopColor="hsl(223,0%,40%)" />
                </linearGradient>
              </defs>
              <circle
                className="pl__ring"
                cx="100"
                cy="100"
                r="82"
                fill="none"
                stroke="url(#pl-grad1)"
                strokeWidth="20"
                strokeDasharray="0 257 1 257"
                strokeDashoffset="0.01"
                strokeLinecap="round"
                transform="rotate(-90,100,100)"
              />
              <line
                className="pl__ball"
                stroke="url(#pl-grad2)"
                x1="100"
                y1="18"
                x2="100.01"
                y2="182"
                strokeWidth="20"
                strokeDasharray="1 165"
                strokeLinecap="round"
              />
            </svg>
          </div>
        </div>
      </div>
    </>
  );
}

export default Loading;
